import { Component, ElementRef, EventEmitter, Inject, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { EXPORT_TO_EXCEL_OVERLAY_DATA } from './export/export-to-excel-overlay-tokens';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators, ValidatorFn, ValidationErrors } from '@angular/forms';
import { ThemeService } from './../../services/theme/theme.service';
import { ExportOptionsService } from './../../services/export/export-options/export-options.service';
import { Observable, of } from 'rxjs';
import { ExcelService } from './../../services/excel/excel.service';

@Component({
  selector: 'tablejs-export-to-excel-overlay',
  templateUrl: './export-to-excel-overlay.component.html',
  styleUrls: ['./export-to-excel-overlay.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ExportToExcelOverlayComponent implements OnInit {

  @Output() public exportOptionEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() public closeEmitter: EventEmitter<void> = new EventEmitter<void>();

  public form: FormGroup;
  public maxSheetsPerDocument: number = 100;
  public exportableItems: any[];

  constructor(
    @Inject(EXPORT_TO_EXCEL_OVERLAY_DATA) public data: any,
    public fb: FormBuilder,
    public themeService: ThemeService,
    public exportOptionsService: ExportOptionsService,
    public elementRef: ElementRef,
    public excelService: ExcelService
  ) { }

  ngOnInit() {
    const lastEmit: any = this.exportOptionsService.lastEmit;
    this.exportOptionsService.exportOptionEmitter = this.exportOptionEmitter;

    this.themeService.themes.forEach(theme => {
      this.elementRef.nativeElement.classList.add(theme);
    });

    this.form = this.fb.group({
      selectedRecordsOnly: [lastEmit ? lastEmit.selectedRecordsOnly : false, [Validators.required]],
      exportCustomerData: [lastEmit ? lastEmit.exportCustomerData : true, [Validators.required]],
      exportTransactionData: [lastEmit ? lastEmit.exportTransactionData : false, [Validators.required]]
    });

    this.form.valueChanges.subscribe(res => {
      this.updateExportableItems(this.form.controls['selectedRecordsOnly'].value);
    });

    this.updateExportableItems(this.form.controls['selectedRecordsOnly'].value);
  }

  public updateExportableItems(selectedOnly: boolean) {
    this.exportableItems = selectedOnly ? this.getSelectedExportableItems() : this.data.exportableItems;
  }
  public getSelectedExportableItems(): any[] {
    return this.data.exportableItems.filter(item => item.selected);
  }

  public onSubmit(form: FormGroup) {
    if (form.invalid) {
      return;
    }

    const formData = form.value;

    this.excelService.saveWorkbookFromData(this.exportableItems, 'customer_data', this.form.controls['exportCustomerData'].value, this.form.controls['exportTransactionData'].value);

    this.exportOptionEmitter.emit({
      selectedRecordsOnly: formData.selectedRecordsOnly,
      exportCustomerData: formData.exportCustomerData,
      exportTransactionData: formData.exportTransactionData
    });
  }

  public closeExportToExcelModal() {
    this.closeEmitter.emit();
  }

}
