import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'tablejs-fps-meter',
  templateUrl: './fps-meter.component.html',
  styleUrls: ['./fps-meter.component.scss'],
  host: { class: 'fps' },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FpsMeterComponent implements OnInit {

  fps = 0;
  lastLoop: number = Date.now();

  constructor(private cdr: ChangeDetectorRef, ) { }

  ngOnInit() {
    this.timeout();
  }

  frameLoop(): number {
    const thisLoop: number = Date.now();
    this.fps = Math.round(1000 / (thisLoop - this.lastLoop));
    this.lastLoop = thisLoop;
    this.cdr.detectChanges();
    return this.fps;
  }

  timeout() {
    setTimeout(() => {
        this.frameLoop();
        this.timeout();
    }, 1000 / 60);
  }

}
