import { Injectable, EventEmitter } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PrintOptionsService {

  private _printOptionEmitter: EventEmitter<any>;
  private _lastEmit: any;

  public set printOptionEmitter(optionEmitter: EventEmitter<any>) {
    this._printOptionEmitter = optionEmitter;
    this._printOptionEmitter.subscribe(emit => {
      this._lastEmit = emit;
    });
  }

  public get printOptionEmitter() {
    return this._printOptionEmitter;
  }

  public get lastEmit(): any {
    return this._lastEmit;
  }

  constructor() { }
}
