import { Injectable } from '@angular/core';
import { IFilterOption } from './../../shared/interfaces/i-filter-option';
import { FilterType } from '../../shared/classes/filter-type';

@Injectable({
  providedIn: 'root'
})
export class FilterService {

  constructor() { }

  public iterate(obj: any, option: IFilterOption): boolean {
    let containsValue: boolean = false;
    Object.keys(obj).forEach(key => {
      if (obj[key] instanceof Object) {
        if (this.iterate(obj[key], option)) {
          containsValue = true;
        }
      } else {
        if (obj[key].toString().toLowerCase().indexOf(option.validFilterValues[0].toLowerCase()) !== -1) {
          containsValue = true;
        }
      }
    });
    return containsValue;
  }
  public filter(arr: any[], option: IFilterOption, deep: boolean = false): any[] {
    switch (option.filterType) {

      case FilterType.EXISTS_IN_TEXT:
        return arr.filter(item => {
          let props: string[] = Object.keys(item);
          for (let prop of props) {
            if (deep && (item[prop] instanceof Array || item[prop] instanceof Object)) {
              if (this.iterate(item, option)) {
                return true;
              }
            }
            if (item[prop].toString().toLowerCase().indexOf(option.validFilterValues[0].toLowerCase()) !== -1) {
              return true;
            }
          }
          return false;
        });

      default:
        return arr.filter(item => {
          let prop = item[option.varName];
          for (let i = 0; i < option.validFilterValues.length; i++) {
            switch (option.filterType) {
              case FilterType.SELECTED_AND_UNSELECTED:
                if (this.filterByExact(prop, true) || this.filterByExact(prop, false)) {
                  return true;
                }
                break;
              case FilterType.SELECTED:
                if (this.filterByExact(prop, true)) {
                  return true;
                }
                break;
              case FilterType.UNSELECTED:
                if (this.filterByExact(prop, false)) {
                  return true;
                }
                break;
              case FilterType.EXACT_MATCH:
                if (this.filterByExact(prop, option.validFilterValues[i])) {
                  return true;
                }
                break;
              case FilterType.AREA_CODE:
                if (this.filterByAreaCode(prop, option.validFilterValues[i])) {
                  return true;
                }
                break;
            }
          }
          return false;
        });
    }
  }

  public filterByExact(prop: any, filterValue: any): boolean {
    return prop === filterValue;
  }
  public filterByAreaCode(prop: any, filterValue: any): boolean {
    return prop.toString().substring(0, 3) === filterValue.toString();
  }
  public filterByText(): boolean {
    return true;
  }
  public getExistingFilter(filterOptions: IFilterOption[], filterOption: IFilterOption): IFilterOption {
    return filterOptions.filter(item => {
      return item.varName === filterOption.varName && item.filterType === filterOption.filterType;
    })[0];
  }
}
