import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { GridDataService } from './../../../services/grid-data/grid-data.service';
import { FilterService } from './../../../services/filter/filter.service';
import { ThemeService } from './../../../services/theme/theme.service';
import { IFilterOption } from '../../../shared/interfaces/i-filter-option';
import { FilterType } from '../../../shared/classes/filter-type';
import { ColumnReorderEvent } from './../../../tablejs/shared/classes/events/column-reorder-event';
import { ColumnResizeEvent } from './../../../tablejs/shared/classes/events/column-resize-event';

@Component({
  selector: 'tablejs-transaction-grid',
  templateUrl: './transaction-grid.component.html',
  styleUrls: ['./transaction-grid.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TransactionGridComponent implements OnInit, AfterViewInit {

  @ViewChild('viewPort', { static: false }) viewPort: CdkVirtualScrollViewport;
  @Input() item: any;
  @Input() resizeEnabled = false;
  @Input() reorderEnabled = false;
  @Input() editableCellsEnabled = false;
  @Input() sortableColumnsEnabled = false;

  constructor(
    public elementRef: ElementRef,
    public themeService: ThemeService,
    public gridDataService: GridDataService,
    public filterService: FilterService
  ) {
  }

  ngOnInit() {
    if (this.item.lastFilterText === undefined || this.item.lastFilterText === null) {
      this.item.lastFilterText = '';
    }
  }

  ngAfterViewInit() {
  }

  getLastFilterText(): string {
    return this.item.lastFilterText === undefined ? '' : this.item.lastFilterText;
  }

  public getFilteredSales(item: any): any[] {
    if (!item.sortedAndFilteredItems) {
      item.sortedAndFilteredItems = item.sales.concat();
    }
    return item.sortedAndFilteredItems;
  }

  public filterTransactionByText(e: KeyboardEvent): void {
    const target: HTMLInputElement = e.target as HTMLInputElement;
    this.item.lastFilterText = target.value;
    let filterOption: IFilterOption = {
      varName: null,
      filterType: FilterType.EXISTS_IN_TEXT,
      validFilterValues: [target.value]
    };
    if (!this.item.filterOptions) {
      this.item.filterOptions = [];
    }
    let existingFilter = this.filterService.getExistingFilter(this.item.filterOptions, filterOption);
    if (existingFilter) {
      existingFilter.validFilterValues = [target.value];
    } else {
      this.item.filterOptions.push(filterOption);
    }
    this.item.sortedAndFilteredItems = this.filterItems(this.item.sales, this.item.filterOptions);
    if (!!this.item.lastSortVarName) {
      this.item.sortDirection *= -1;
      this.sortByVar(this.item.lastSortVarName);
    }
    this.viewPort.scrollToOffset(0);
  }

  public filterItems(arr: any[], filterOptions: IFilterOption[]): any[] {
    let copyArr = arr.concat();
    const len = filterOptions.length;
    for (let i = 0; i < len; i++) {
      const filterOption: IFilterOption = filterOptions[i];
      copyArr = this.filterService.filter(copyArr, filterOption);
    }
    return copyArr;
  }

  public getRoundedRatingText(rating: number): string {
    rating = Math.round(rating) / 100;
    let starText = ' star';
    if (rating !== 1) {
      starText += 's';
    }
    return (rating).toString() + starText;
  }

  public sortByVar(varName: string): void {
    if (!this.item.filterOptions) {
      this.item.filterOptions = [];
    }
    this.item.sortedAndFilteredItems = this.filterItems(this.item.sales, this.item.filterOptions);

    if (this.item.lastSortVar === varName) {
      this.item.sortDirection *= -1;
    } else {
      this.item.sortDirection = 1;
      this.item.lastSortVar = varName;
    }
    this.item.sortedAndFilteredItems = this.item.sortedAndFilteredItems.sort((item1: any, item2: any) => {
      if (item1[varName] > item2[varName]) { return this.item.sortDirection; }
      if (item2[varName] > item1[varName]) { return -1 * this.item.sortDirection; }
      return 0;
    });

    this.item.sortedAndFilteredItems = [].concat(this.item.sortedAndFilteredItems);
    this.item.lastSortVarName = varName;
  }

  sortedDown(varName: string): boolean {
    return varName === this.item.lastSortVar && this.item.sortDirection === 1;
  }

  sortedUp(varName: string): boolean {
    return varName === this.item.lastSortVar && this.item.sortDirection === -1;
  }

  // Triggered when a details column row resize begins
  detailsColumnResizeStart(e: ColumnResizeEvent): void { }

  // Triggered when a details column row is resized via a mouse movemenet
  detailsColumnResize(e: ColumnResizeEvent): void { }

  // Triggered when a details column row is resize is completed
  detailsColumnResizeEnd(e: ColumnResizeEvent): void { }

  // Triggered when a parent column reorder begins
  detailsColumnReorderStart(e: ColumnReorderEvent): void { }

  // Triggered when a parent column is being reordered
  detailsColumnReorder(e: ColumnReorderEvent): void { }

  // Triggered when a parent column reorder ends
  detailsColumnReorderEnd(e: ColumnReorderEvent): void { }
}
