import { Injectable, EventEmitter } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ExportOptionsService {

  private _exportOptionEmitter: EventEmitter<any>;
  private _lastEmit: any;

  public set exportOptionEmitter(optionEmitter: EventEmitter<any>) {
    this._exportOptionEmitter = optionEmitter;
    this._exportOptionEmitter.subscribe(emit => {
      this._lastEmit = emit;
    });
  }

  public get exportOptionEmitter() {
    return this._exportOptionEmitter;
  }

  public get lastEmit(): any {
    return this._lastEmit;
  }

  constructor() { }
}
