import { AfterViewInit, Component, EventEmitter, Inject, OnInit, Output, ViewChild, ElementRef, ViewEncapsulation } from '@angular/core';
import { SELECTED_OVERLAY_DATA } from './selected/selected-overlay-tokens';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { IFilterOption } from './../../shared/interfaces/i-filter-option';
import { FilterType } from '../../shared/classes/filter-type';
import { ThemeService } from './../../services/theme/theme.service';

@Component({
  selector: 'tablejs-selected-filter',
  templateUrl: './selected-filter.component.html',
  styleUrls: ['./selected-filter.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SelectedFilterComponent implements OnInit, AfterViewInit {

  public form: FormGroup;
  private preselectedOptions: boolean[];
  @Output() public filterOptionEmitter: EventEmitter<IFilterOption> = new EventEmitter<IFilterOption>();
  @ViewChild('selectAll', { static: false }) selectAllInput: ElementRef;

  constructor(
    @Inject(SELECTED_OVERLAY_DATA) public data: any,
    private fb: FormBuilder,
    private themeService: ThemeService,
    private elementRef: ElementRef
  ) {
    this.preselectedOptions = this.data.selectedRowVisibilityOptions;
  }

  public ngOnInit() {
    this.themeService.themes.forEach(theme => {
      this.elementRef.nativeElement.classList.add(theme);
    });
    this.form = this.fb.group({
      selectAll: [this.preselectedOptions.length !== 0, Validators.required],
      selectedRows: [this.preselectedOptions.indexOf(true) !== -1, Validators.required],
      unselectedRows: [this.preselectedOptions.indexOf(false) !== -1, Validators.required]
    });
  }

  public ngAfterViewInit() {
  }

  public mixedCheckValues(): boolean {
    if (this.form.controls['selectAll'].value === false) {
      return false;
    }
    let hasTrue: boolean = false;
    let hasFalse: boolean = false;
    Object.keys(this.form.controls).forEach(key => {
      if (key !== 'selectAll') {
        if (this.form.controls[key].value === true) {
          hasTrue = true;
        } else {
          hasFalse = true;
        }
      }
    });
    return hasTrue && hasFalse;
  }

  public checkboxChanged(e: Event): void {
    const checkbox: any = e.target;
    const id = checkbox.id;
    if (e.target === this.selectAllInput.nativeElement) {
      const selectAllValue = this.form.controls['selectAll'].value;
      this.setAllFormControlsTo(selectAllValue);
    } else {
      if (this.form.controls[id].value === true) {
        this.form.controls['selectAll'].setValue(true);
      } else {
        let hasTrue: boolean = false;
        let hasFalse: boolean = false;
        Object.keys(this.form.controls).forEach(key => {
          if (key !== 'selectAll') {
            if (this.form.controls[key].value === true) {
              hasTrue = true;
            } else {
              hasFalse = true;
            }
          }
        });
        if (!hasTrue && hasFalse) {
          this.form.controls['selectAll'].setValue(false);
        }
      }
    }
    this.onSubmit(this.form);
  }

  public setAllFormControlsTo(val: boolean): void {
    Object.keys(this.form.controls).forEach(key => {
      if (key !== 'selectAll') {
        this.form.controls[key].setValue(val);
      }
    });
  }

  public resetFilter(e: MouseEvent): void {
    this.form.controls['selectAll'].setValue(true);
    this.setAllFormControlsTo(true);
  }

  public onSubmit(form: FormGroup) {
    const formData = form.value;
    let filterType: string = FilterType.EXACT_MATCH;
    let validFilterValues: boolean[];

    if (this.form.controls['selectedRows'].value && this.form.controls['unselectedRows'].value) {
      validFilterValues = [true, false];
      // filterType = FilterType.EXACT_MATCH;
    } else if (this.form.controls['selectedRows'].value) {
      validFilterValues = [true];
      // filterType = FilterType.SELECTED;
    } else if (this.form.controls['unselectedRows'].value) {
      // filterType = FilterType.UNSELECTED;
      validFilterValues = [false];
    } else {
      validFilterValues = [];
    }

    const filterOption: any = {
      varName: 'selected',
      filterType,
      validFilterValues
    };
    this.filterOptionEmitter.emit(filterOption);
  }

}
