import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CellValidatorService {

  constructor() { }

  public hasFirstAndLastName(content: string): boolean {
    let names: string[] = content.split(' ');
    return names.length === 2 && names[1].length > 0; // regEx.test(content);
  }

  public isNotEmpty(content: string): boolean {
    return content.length > 0 && content.charAt(0) !== ' ';
  }

  public itemExistsInList(item: string, list: string[]): boolean {
    return list.indexOf(item) !== -1;
  }

  // Verify if email meets all requirements
  public emailIsValid(email: string): boolean {
    email = email.toLowerCase();
    let re: RegExp = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()\.,;\s@\"]+\.{0,1})+([^<>()\.,;:\s@\"]{2,}|[\d\.]+))$/;
    return re.test(email);
  }
}
