import { EventEmitter, Injectable, Output } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ThemeService {

  @Output() public themeChanged: EventEmitter<any> = new EventEmitter<any>();
  public themeColor: string = 'blue';
  public themeSize: string = 'reg';
  public get theme(): string {
    return 'theme-' + this.themeSize + ' theme-' + this.themeColor;
  }
  public get themes(): string[] {
    return this.theme.split(' ');
  }

  constructor() { }

  public setThemeColor(color: string): void {
    this.themeColor = color;
    this.themeChanged.emit();
  }
  public setThemeSize(size: string): void {
    this.themeSize = size;
    this.themeChanged.emit();
  }

  public getFullTableHeight(numOfItems: number): number {
    switch (this.themeSize) {
      case 'extra-sm':
        return numOfItems * 31;
      case 'sm':
        return numOfItems * 35.1875;
      case 'lg':
        return numOfItems * 51;
      case 'extra-lg':
        return numOfItems * 62;
      default:
        return numOfItems * 41;
    }
  }

}
