import { AfterViewInit, Component, ComponentRef, ChangeDetectorRef, HostListener, InjectionToken, Injector, QueryList, OnInit, ViewChild, ViewChildren, ViewEncapsulation } from '@angular/core';
import { Overlay, OverlayConfig, OverlayRef, PositionStrategy, ScrollStrategy } from '@angular/cdk/overlay';
import { ComponentPortal, PortalInjector } from '@angular/cdk/portal';
import { CellValidatorService } from './../../services/cell-validator/cell-validator.service';
import { ColumnReorderEvent } from './../../tablejs/shared/classes/events/column-reorder-event';
import { ColumnResizeEvent } from './../../tablejs/shared/classes/events/column-resize-event';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { PhoneFilterComponent } from './../phone-filter/phone-filter.component';
import { SelectedFilterComponent } from './../selected-filter/selected-filter.component';
import { FILTER_OVERLAY_DATA } from './../phone-filter/filters/filter-overlay-tokens';
import { PRINT_OVERLAY_DATA } from './../print-overlay/print/print-overlay-tokens';
import { EXPORT_TO_EXCEL_OVERLAY_DATA } from './../export-to-excel-overlay/export/export-to-excel-overlay-tokens';
import { SELECTED_OVERLAY_DATA } from './../selected-filter/selected/selected-overlay-tokens';
import { FilterService } from './../../services/filter/filter.service';
import { IFilterOption } from '../../shared/interfaces/i-filter-option';
import { GridDataService } from './../../services/grid-data/grid-data.service';
import { FilterType } from '../../shared/classes/filter-type';
import { ThemeService } from './../../services/theme/theme.service';
import { PrintOverlayComponent } from './../print-overlay/print-overlay.component';
import { ExportToExcelOverlayComponent } from './../export-to-excel-overlay/export-to-excel-overlay.component';
import { TransactionGridComponent } from './transaction-grid/transaction-grid.component';
import { ExcelService } from './../../services/excel/excel.service';

@Component({
  selector: 'tablejs-grid-example',
  templateUrl: './grid-example.component.html',
  styleUrls: ['./grid-example.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class GridExampleComponent implements OnInit, AfterViewInit {

  @ViewChild('mainViewPort', { static: false }) mainViewPort: CdkVirtualScrollViewport;
  @ViewChildren(TransactionGridComponent) transactionViewPorts: QueryList<TransactionGridComponent>;

  window: any = (<any>window);
  resizeEnabled = false;
  reorderEnabled = false;
  editableCellsEnabled = false;
  sortableColumnsEnabled = false;
  baseItems: any[] = [];
  sortedAndFilteredItems: any[];
  preventInitialAnimation = true;
  lastSortVar: string;
  sortDirection = 1;
  selectedAreaCodes: number[];
  selectedRowVisibilityOptions: boolean[] = [true, false];
  filterOptions: IFilterOption[] = [];
  lastSortVarName: string;
  defaultSelectValue = 1000;
  prePrintHeight: number;
  printing = false;
  printInterval: any;
  printPreviewInterval: any;
  rowsChecked = 0;
  printItems: any[];

  constructor(
    public cellValidatorService: CellValidatorService,
    public overlay: Overlay,
    public injector: Injector,
    public filterService: FilterService,
    public gridDataService: GridDataService,
    public themeService: ThemeService,
    public excelService: ExcelService,
    public cdr: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.selectedAreaCodes = this.gridDataService.areaCodes.concat();
    this.setItems(this.defaultSelectValue);
  }
  ngAfterViewInit() {
  }

  @HostListener('document:keydown', ['$event']) onKeydownHandler(event: KeyboardEvent) {
    if (event.metaKey || event.ctrlKey) {
      if (event.key.toLowerCase() === 'p') {
        event.preventDefault();
        this.openPrintOverlay();
      }
    }
  }
  @HostListener('window:beforeprint', ['$event']) public beforePrint() {
  }
  @HostListener('window:afterprint', ['$event']) public afterPrint() {
    this.resetItemsAfterPrint();
  }

  private resetItemsAfterPrint() {
    this.printing = false;
    const items = this.printItems;
    this.sortedAndFilteredItems = [];

    setTimeout(() => {
      this.sortedAndFilteredItems = items;
      this.sortedAndFilteredItems.forEach(item => {
        item.printing = false;
        item.collapsed = item.wasCollapsedBeforePrint !== false;
      });
      this.cdr.detectChanges();
    }, 1);
  }

  public print(e: MouseEvent) {
    e.preventDefault();
    e.stopImmediatePropagation();
    this.openPrintPreview();
  }

  public exportToExcel(e: MouseEvent) {
    this.openExportToExcelOverlay();
  }

  private openPrintPreview() {
    clearInterval(this.printPreviewInterval);
    this.openPrintOverlay();
  }

  public setItems(itemCount: number): void {
    this.baseItems = this.gridDataService.generateGridData(itemCount);
    this.baseItems.forEach(item => {
      item.selected = false;
      item.printing = false;
    });
    this.sortedAndFilteredItems = this.baseItems;
    if (!!this.lastSortVarName) {
      this.sortDirection *= -1;
      this.sortByVar(this.lastSortVarName);
    }
  }

  public filterGridByText(e: KeyboardEvent): void {
    const target: HTMLInputElement = e.target as HTMLInputElement;
    let filterOption: IFilterOption = {
      varName: null,
      filterType: FilterType.EXISTS_IN_TEXT,
      validFilterValues: [target.value]
    };
    let existingFilter = this.filterService.getExistingFilter(this.filterOptions, filterOption);
    if (existingFilter) {
      existingFilter.validFilterValues = [target.value];
    } else {
      this.filterOptions.push(filterOption);
    }
    this.sortedAndFilteredItems = this.filterItems(this.baseItems, this.filterOptions);
    if (!!this.lastSortVarName) {
      this.sortDirection *= -1;
      this.sortByVar(this.lastSortVarName);
    }
    if (this.mainViewPort) {
      this.mainViewPort.scrollToOffset(0);
    }
  }

  public filterItems(arr: any[], filterOptions: IFilterOption[]): any[] {
    let copyArr = arr.concat();
    const len = filterOptions.length;
    for (let i = 0; i < len; i++) {
      const filterOption: IFilterOption = filterOptions[i];
      copyArr = this.filterService.filter(copyArr, filterOption);
    }
    return copyArr;
  }
  calculateUserRatingPerc(rating: number): number {
    return rating / 5;
  }
  getRoundedRatingText(rating: number): string {
    rating = Math.round(rating) / 100;
    let starText = ' star';
    if (rating !== 1) {
      starText += 's';
    }
    return (rating).toString() + starText;
  }

  public rowsSelected(e: Event): void {
    const target: HTMLSelectElement = e.target as HTMLSelectElement;
    this.setItems(Number(target.value));
    this.mainViewPort.scrollToOffset(0);
  }
  public themeColorSelected(e: Event): void {
    const target: HTMLSelectElement = e.target as HTMLSelectElement;
    this.themeService.setThemeColor(target.value);
  }
  public themeSizeSelected(e: Event): void {
    const target: HTMLSelectElement = e.target as HTMLSelectElement;
    this.themeService.setThemeSize(target.value);
  }


  toggleResize(e: Event) {
    this.resizeEnabled = !this.resizeEnabled;
  }
  toggleReorder(e: Event) {
    this.reorderEnabled = !this.reorderEnabled;
  }
  toggleCellEditability(e: Event) {
    this.editableCellsEnabled = !this.editableCellsEnabled;
  }
  toggleColumnSortability(e: Event) {
    this.sortableColumnsEnabled = !this.sortableColumnsEnabled;
  }

  toggleCollapse(item: any) {
    item.collapsed = !item.collapsed;
    item.wasCollapsedBeforePrint = item.collapsed;
  }

  // Used for preventing calculation issues from animations while scrolling the infinite scroll viewport
  animationEnd(expandData: any, e: AnimationEvent) {
    expandData.wasOpened = e.animationName === 'uncollapseRowAnimation';
  }

  /* Start Editable Cell Functions */
  onEditableCellInput(item: any, varName: string, content: any) {
    if (content.currentValue !== content.lastValidInput) {
      item[varName] = content.originalValue;
    } else {
      item[varName] = content.currentValue;
    }
    console.log('updated to: ' + item[varName]);
  }
  onEditableCellValidated(e: Event) { }
  onEditableCellFocusOut(e: Event) { }
  /* End Editable Cell Functions */

  /* Start Sort Functions */
  sortByVar(varName: string): void {
    this.sortedAndFilteredItems = this.filterItems(this.baseItems, this.filterOptions);

    if (this.lastSortVar === varName) {
      this.sortDirection *= -1;
    } else {
      this.sortDirection = 1;
      this.lastSortVar = varName;
    }
    this.sortedAndFilteredItems = this.sortedAndFilteredItems.sort((item1: any, item2: any) => {
      if (item1[varName] > item2[varName]) { return this.sortDirection; }
      if (item2[varName] > item1[varName]) { return -1 * this.sortDirection; }
      return 0;
    });

    this.sortedAndFilteredItems = [].concat(this.sortedAndFilteredItems);
    this.lastSortVarName = varName;
  }

  private openExportToExcelOverlay(): void {
    let positionStrategy = this.overlay
      .position()
      .global()
      .centerHorizontally()
      .centerVertically();

    const overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      backdropClass: 'export-to-excel-overlay-bg',
      panelClass: 'export-to-excel-overlay-panel',
      scrollStrategy: this.overlay.scrollStrategies.block(),
      positionStrategy
    });

    const overlayRef = this.overlay.create(overlayConfig);

    // Create ComponentPortal that can be attached to a PortalHost
    const injector = this.createInjector({ exportableItems: this.sortedAndFilteredItems }, EXPORT_TO_EXCEL_OVERLAY_DATA);

    const containerPortal = new ComponentPortal(ExportToExcelOverlayComponent, null, injector);
    const containerRef: ComponentRef<any> = overlayRef.attach(containerPortal);

    const component: ExportToExcelOverlayComponent = containerRef.instance;

    component.closeEmitter.subscribe(() => {
      overlayRef.detach();
    });
  }

  private openPrintOverlay(): void {
    let positionStrategy = this.overlay
      .position()
      .global()
      .centerHorizontally()
      .centerVertically();

    const overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      backdropClass: 'print-overlay-bg',
      panelClass: 'print-overlay-panel',
      scrollStrategy: this.overlay.scrollStrategies.block(),
      positionStrategy
    });

    const overlayRef = this.overlay.create(overlayConfig);

    // Create ComponentPortal that can be attached to a PortalHost
    const injector = this.createInjector({ printableItems: this.sortedAndFilteredItems, containsPrintableItems: true }, PRINT_OVERLAY_DATA);

    const containerPortal = new ComponentPortal(PrintOverlayComponent, null, injector);
    const containerRef: ComponentRef<any> = overlayRef.attach(containerPortal);

    const component: PrintOverlayComponent = containerRef.instance;

    component.closeEmitter.subscribe(() => {
      overlayRef.detach();
    });

    component.printOptionEmitter.subscribe(
      (printOptions: any) => {
        const numOfRows: number = printOptions.rowEnd - printOptions.rowStart + 1;
        this.printItems = this.sortedAndFilteredItems.concat();
        if (printOptions.selectedRowsOnly) {
          const printFilterOption: IFilterOption = {
            varName: 'selected',
            filterType: FilterType.EXACT_MATCH,
            validFilterValues: [true]
          };
          this.sortedAndFilteredItems = this.filterService.filter(this.sortedAndFilteredItems, printFilterOption);
        }
        this.sortedAndFilteredItems = this.sortedAndFilteredItems.slice(printOptions.rowStart - 1, printOptions.rowEnd);

        if (!printOptions.includeTransactions) {
          this.sortedAndFilteredItems.forEach(item => {
            item.wasCollapsedBeforePrint = item.collapsed;
            item.collapsed = true;
          });
        } else {
          this.sortedAndFilteredItems.forEach(item => {
            item.printing = true;
            item.wasCollapsedBeforePrint = item.collapsed;
            item.collapsed = false;
          });
        }

        const hgt = printOptions.printHeight;
        this.mainViewPort.elementRef.nativeElement.style.height = (hgt).toString() + 'px';
        this.mainViewPort.checkViewportSize();

        this.printing = true;

        const performAnimation = () => {
          if (!this.mainViewPort) {
            console.log('no valid print data');
            clearInterval(this.printInterval);
            injector.get(PRINT_OVERLAY_DATA).containsPrintableItems = false;
            this.resetItemsAfterPrint();
            return;
          }
          this.transactionViewPorts.forEach(transactionViewport => {
            if (!transactionViewport.item.collapsed) {
              transactionViewport.viewPort.checkViewportSize();
            }
          });
          this.mainViewPort.checkViewportSize();

          if (this.mainViewPort.elementRef.nativeElement.clientHeight === hgt) {
            clearInterval(this.printInterval);
            overlayRef.detach();
            this.printPreviewInterval = setTimeout(() => {
              window.print(); return false;
            }, 1);
          } else {
            setTimeout(performAnimation, 50);
          }
        };

        this.printInterval = setTimeout(performAnimation, 50);

      }
    );
  }



  openPhoneSort(e: MouseEvent): void {
    e.preventDefault();
    e.stopImmediatePropagation();

    const el = e.target as HTMLElement;
    let positionStrategy = this.overlay
      .position()
      .flexibleConnectedTo(el)
      .withPositions([
        { originX: 'start', originY: 'bottom', overlayX: 'start', overlayY: 'top' }
      ])
      .withDefaultOffsetX(-200)
      .withDefaultOffsetY(10);

    const overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      backdropClass: 'phone-filter-bg',
      panelClass: 'phone-filter-panel',
      scrollStrategy: this.overlay.scrollStrategies.close(),
      positionStrategy
    });

    const overlayRef = this.overlay.create(overlayConfig);

    // Create ComponentPortal that can be attached to a PortalHost
    const injector = this.createInjector({ allAreaCodes: this.gridDataService.areaCodes, selectedAreaCodes: this.selectedAreaCodes }, FILTER_OVERLAY_DATA);

    const containerPortal = new ComponentPortal(PhoneFilterComponent, null, injector);
    const containerRef: ComponentRef<any> = overlayRef.attach(containerPortal);

    const component: PhoneFilterComponent = containerRef.instance;

    component.filterOptionEmitter.subscribe(
      (filterOption: IFilterOption) => {
        let existingFilter = this.filterService.getExistingFilter(this.filterOptions, filterOption);
        if (existingFilter) {
          existingFilter.validFilterValues = filterOption.validFilterValues;
        } else {
          this.filterOptions.push(filterOption);
        }
        this.selectedAreaCodes = filterOption.validFilterValues.concat();
        this.sortedAndFilteredItems = this.filterItems(this.baseItems, this.filterOptions);
        if (!!this.lastSortVarName) {
          this.sortDirection *= -1;
          this.sortByVar(this.lastSortVarName);
        }
      }
    );

    overlayRef.backdropClick().subscribe(() => {
      overlayRef.detach();
    });
  }

  openSelectedSort(e: MouseEvent): void {
    e.preventDefault();
    e.stopImmediatePropagation();

    const el = e.target as HTMLElement;
    let positionStrategy = this.overlay
      .position()
      .flexibleConnectedTo(el)
      .withPositions([
        { originX: 'start', originY: 'bottom', overlayX: 'start', overlayY: 'top' }
      ])
      .withDefaultOffsetX(-10)
      .withDefaultOffsetY(10);

    const overlayConfig = new OverlayConfig({
      hasBackdrop: true,
      backdropClass: 'selected-filter-bg',
      panelClass: 'selected-filter-panel',
      scrollStrategy: this.overlay.scrollStrategies.close(),
      positionStrategy
    });

    const overlayRef = this.overlay.create(overlayConfig);

    // Create ComponentPortal that can be attached to a PortalHost
    const injector = this.createInjector({ selectedRowVisibilityOptions: this.selectedRowVisibilityOptions }, SELECTED_OVERLAY_DATA);

    const containerPortal = new ComponentPortal(SelectedFilterComponent, null, injector);
    const containerRef: ComponentRef<any> = overlayRef.attach(containerPortal);

    const component: SelectedFilterComponent = containerRef.instance;

    component.filterOptionEmitter.subscribe(
      (filterOption: IFilterOption) => {
        let existingFilter = this.filterService.getExistingFilter(this.filterOptions, filterOption);
        if (existingFilter) {
          existingFilter.validFilterValues = filterOption.validFilterValues;
        } else {
          this.filterOptions.push(filterOption);
        }
        this.selectedRowVisibilityOptions = filterOption.validFilterValues;
        this.sortedAndFilteredItems = this.filterItems(this.baseItems, this.filterOptions);
        if (!!this.lastSortVarName) {
          this.sortDirection *= -1;
          this.sortByVar(this.lastSortVarName);
        }
      }
    );

    overlayRef.backdropClick().subscribe(() => {
      overlayRef.detach();
    });
  }

  private createInjector(dataToPass, token: any): PortalInjector {
    const injectorTokens = new WeakMap();
    injectorTokens.set(token, dataToPass);
    return new PortalInjector(this.injector, injectorTokens);
  }

  sortedDown(varName: string): boolean {
    return varName === this.lastSortVar && this.sortDirection === 1;
  }

  sortedUp(varName: string): boolean {
    return varName === this.lastSortVar && this.sortDirection === -1;
  }

  setRowCheckId(index: number) {
    return 'row-selection-check-' + index;
  }

  rowSelectionChanged(e: Event, item: any) {
    const target: HTMLInputElement = e.target as HTMLInputElement;
    item.selected = target.checked;
    if (target.checked) {
      this.rowsChecked++;
    } else {
      this.rowsChecked--;
    }
    this.sortedAndFilteredItems = this.filterItems(this.baseItems, this.filterOptions);
    if (!!this.lastSortVarName) {
      this.sortDirection *= -1;
      this.sortByVar(this.lastSortVarName);
    }
  }

  selectAllRowsChanged(e: Event) {
    const target: HTMLInputElement = e.target as HTMLInputElement;
    this.baseItems.forEach(item => {
      item.selected = target.checked;
    });
    this.rowsChecked = target.checked ? this.baseItems.length : 0;
  }

  isIndeterminate() {
    return this.rowsChecked !== 0 && this.rowsChecked !== this.baseItems.length;
  }
  /* End Sort Functions */

  // Filters out nodes on the column drag and drop ghost object
  dragDropFilter(el: HTMLElement) {
    return (el.tagName !== 'TABLEJS-HORIZ-RESIZE-GRIP') && !(el.classList && el.classList.contains('sort-icon'));
  }

  /* Start Column Event Triggers */

  // Triggered when a parent column row resize begins
  columnResizeStart(e: ColumnResizeEvent): void { }

  // Triggered when a parent column row is resized via a mouse movemenet
  columnResize(e: ColumnResizeEvent): void { }

  // Triggered when a parent column row is resize is completed
  columnResizeEnd(e: ColumnResizeEvent): void { }

  // Triggered when a parent column reorder begins
  columnReorderStart(e: ColumnReorderEvent): void { }

  // Triggered when a parent column is being reordered
  columnReorder(e: ColumnReorderEvent): void { }

  // Triggered when a parent column reorder ends
  columnReorderEnd(e: ColumnReorderEvent): void { }

  /* End Column Event Triggers */

}
